"use client"

import { useEffect, useState, useCallback, useRef } from "react"
import { useParams, useRouter } from "next/navigation"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { useToast } from "@/components/ui/use-toast"
import { BackButton } from "@/components/ui/BackButton"
import { useAuth } from "@/contexts/AuthContext"
import usuarioService from "@/services/usuarioService"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { AlertCircle, Loader2, ChevronDown, ChevronUp, Check } from "lucide-react"

// Definição de tipos para as permissões
interface Permissao {
  id: string
  label: string
}

// Definição de tipos para as categorias de permissões
interface CategoriaPermissoes {
  [categoria: string]: Permissao[]
}

// Definição das permissões por categoria
const permissoesPorCategoria: CategoriaPermissoes = {
  Estoque: [
    { id: "ESTOQUE_VISUALIZAR", label: "Visualizar estoque" },
    { id: "ESTOQUE_ADICIONAR", label: "Adicionar produtos" },
    { id: "ESTOQUE_EDITAR", label: "Editar produtos" },
    { id: "ESTOQUE_REMOVER", label: "Remover produtos" },
  ],
  Vendas: [
    { id: "VENDAS_VISUALIZAR", label: "Visualizar vendas" },
    { id: "VENDAS_CRIAR", label: "Criar vendas" },
    { id: "VENDAS_CANCELAR", label: "Cancelar vendas" },
    { id: "VENDAS_RELATORIOS", label: "Relatórios de vendas" },
  ],
  Fila: [
    { id: "FILA_VISUALIZAR", label: "Visualizar fila" },
    { id: "FILA_ADICIONAR", label: "Adicionar clientes" },
    { id: "FILA_REMOVER", label: "Remover clientes" },
    { id: "FILA_GERENCIAR", label: "Gerenciar atendimentos" },
  ],
  Usuários: [
    { id: "USUARIOS_VISUALIZAR", label: "Visualizar usuários" },
    { id: "USUARIOS_ADICIONAR", label: "Adicionar usuários" },
    { id: "USUARIOS_EDITAR", label: "Editar usuários" },
    { id: "USUARIOS_REMOVER", label: "Remover usuários" },
    { id: "USUARIOS_PERMISSOES", label: "Gerenciar permissões" },
  ],
  Sistema: [
    { id: "CONFIGURACOES_VISUALIZAR", label: "Visualizar configurações" },
    { id: "CONFIGURACOES_EDITAR", label: "Editar configurações" },
    { id: "RELATORIOS_VISUALIZAR", label: "Visualizar relatórios" },
    { id: "RELATORIOS_EXPORTAR", label: "Exportar relatórios" },
    { id: "BACKUP_GERENCIAR", label: "Gerenciar backups" },
  ],
}

// Função para criar um objeto com todas as permissões definidas como false
const criarPermissoesVazias = (): Record<string, boolean> => {
  const todasPermissoes: Record<string, boolean> = {}
  Object.values(permissoesPorCategoria).forEach((categoria) => {
    categoria.forEach((perm) => {
      todasPermissoes[perm.id] = false
    })
  })
  return todasPermissoes
}

export default function PermissoesUsuarioPage() {
  const { id } = useParams()
  const router = useRouter()
  const { toast } = useToast()
  const { user } = useAuth()

  const [usuario, setUsuario] = useState<any>(null)
  // Inicializar com permissões do localStorage se disponíveis
  const [permissoesSelecionadas, setPermissoesSelecionadas] = useState<Record<string, boolean>>(() => {
    if (typeof window !== "undefined" && id) {
      const storageKey = `permissoes_usuario_${id}`
      const savedPermissoes = localStorage.getItem(storageKey)
      if (savedPermissoes) {
        try {
          return JSON.parse(savedPermissoes)
        } catch (e) {
          console.error("Erro ao carregar permissões do localStorage:", e)
        }
      }
    }
    return criarPermissoesVazias()
  })

  const [permissoesOriginais, setPermissoesOriginais] = useState<Record<string, boolean>>(criarPermissoesVazias())
  const [carregando, setCarregando] = useState(true)
  const [salvando, setSalvando] = useState(false)
  const [erro, setErro] = useState<string | null>(null)
  const [tentativasCarregamento, setTentativasCarregamento] = useState(0)
  const [mostrarErroCompleto, setMostrarErroCompleto] = useState(false)
  const [erroDetalhado, setErroDetalhado] = useState<string | null>(null)
  const [mostrarDebug, setMostrarDebug] = useState(true) // Mostrar debug por padrão
  const [debugInfo, setDebugInfo] = useState<string>("")
  const [permissoesCarregadasDoLocalStorage, setPermissoesCarregadasDoLocalStorage] = useState(false)

  // Usar useRef em vez de useState para o contador de renderizações
  const renderCountRef = useRef(0)

  // Verificar se o usuário tem permissão para gerenciar permissões
  const canManagePermissions =
    user?.permissoes?.includes("USUARIOS_PERMISSOES") || user?.perfil === "Administrador" || user?.perfil === "ADMIN"

  // Função para adicionar logs de debug
  const addDebugLog = useCallback((message: string) => {
    const timestamp = new Date().toISOString().split("T")[1].split(".")[0]
    setDebugInfo((prev: string) => `${prev}\n${timestamp}: ${message}`)
    console.log(`${timestamp}: ${message}`)
  }, [])

  // Substitua a função inicializarPermissoes por esta versão mais robusta:
  const inicializarPermissoes = useCallback(
    (permissoesArray: string[]) => {
      // Verificar primeiro se há permissões no localStorage
      if (typeof window !== "undefined" && id) {
        const storageKey = `permissoes_usuario_${id}`
        const savedPermissoes = localStorage.getItem(storageKey)
        if (savedPermissoes) {
          try {
            const parsedPermissoes = JSON.parse(savedPermissoes)
            addDebugLog(`INICIALIZAÇÃO: Usando permissões do localStorage em vez da API`)

            // Contar permissões ativas no localStorage
            const permissoesAtivas = Object.entries(parsedPermissoes)
              .filter(([_, value]) => value === true)
              .map(([key]) => key)

            addDebugLog(
              `INICIALIZAÇÃO: Permissões ativas no localStorage (${permissoesAtivas.length}): ${JSON.stringify(permissoesAtivas)}`,
            )

            return parsedPermissoes
          } catch (e) {
            addDebugLog(`INICIALIZAÇÃO: Erro ao analisar permissões do localStorage: ${e}`)
          }
        } else {
          addDebugLog(`INICIALIZAÇÃO: Nenhuma permissão encontrada no localStorage para o usuário ${id}`)
        }
      }

      // Se não houver permissões no localStorage, usar as da API
      addDebugLog(`INICIALIZAÇÃO: Usando permissões da API`)

      // Criar um objeto com todas as permissões definidas como false
      const todasPermissoes: Record<string, boolean> = {}

      // Obter todas as permissões possíveis de todas as categorias
      Object.values(permissoesPorCategoria).forEach((categoria) => {
        categoria.forEach((perm) => {
          todasPermissoes[perm.id] = false
        })
      })

      // Marcar as permissões que o usuário possui como true
      if (Array.isArray(permissoesArray)) {
        permissoesArray.forEach((perm) => {
          if (perm in todasPermissoes) {
            todasPermissoes[perm] = true
          }
        })
      }

      // Contar permissões ativas da API
      const permissoesAtivas = permissoesArray.filter((perm) => perm in todasPermissoes)
      addDebugLog(
        `INICIALIZAÇÃO: Permissões ativas da API (${permissoesAtivas.length}): ${JSON.stringify(permissoesAtivas)}`,
      )

      return todasPermissoes
    },
    [addDebugLog, id],
  )

  // Verificar se uma permissão foi alterada
  const permissaoAlterada = useCallback(
    (permissaoId: string) => {
      return permissoesSelecionadas[permissaoId] !== permissoesOriginais[permissaoId]
    },
    [permissoesSelecionadas, permissoesOriginais],
  )

  // Verificar se há alterações não salvas
  const temAlteracoes = useCallback(() => {
    return Object.keys(permissoesSelecionadas).some(
      (permissaoId) => permissoesSelecionadas[permissaoId] !== permissoesOriginais[permissaoId],
    )
  }, [permissoesSelecionadas, permissoesOriginais])

  // Carregar permissões do localStorage
  const carregarPermissoesDoLocalStorage = useCallback(() => {
    if (typeof window !== "undefined" && id) {
      const storageKey = `permissoes_usuario_${id}`
      const savedPermissoes = localStorage.getItem(storageKey)
      if (savedPermissoes) {
        try {
          const parsedPermissoes = JSON.parse(savedPermissoes)
          addDebugLog(`Permissões encontradas no localStorage: ${JSON.stringify(parsedPermissoes)}`)

          // Contar quantas permissões estão ativas
          const permissoesAtivas = Object.entries(parsedPermissoes)
            .filter(([_, value]) => value === true)
            .map(([key]) => key)

          addDebugLog(
            `Permissões ativas no localStorage (${permissoesAtivas.length}): ${JSON.stringify(permissoesAtivas)}`,
          )

          // Atualizar o estado com as permissões do localStorage
          setPermissoesSelecionadas(parsedPermissoes)
          setPermissoesCarregadasDoLocalStorage(true)
          return true
        } catch (e) {
          addDebugLog(`Erro ao analisar permissões do localStorage: ${e}`)
        }
      } else {
        addDebugLog(`Nenhuma permissão encontrada no localStorage para o usuário ${id}`)
      }
    }
    return false
  }, [id, addDebugLog])

  // Contador de renderizações para debug (usando useRef para evitar loop infinito)
  useEffect(() => {
    renderCountRef.current += 1
    addDebugLog(`Componente renderizado (${renderCountRef.current})`)

    // Carregar permissões do localStorage no primeiro render
    carregarPermissoesDoLocalStorage()
  }, [addDebugLog, carregarPermissoesDoLocalStorage])

  // Substitua o useEffect que carrega os dados do usuário por esta versão:
  useEffect(() => {
    const carregarDados = async () => {
      if (!id) return

      // Verificar se o usuário tem permissão
      if (!canManagePermissions) {
        toast({
          title: "Acesso negado",
          description: "Você não tem permissão para gerenciar permissões de usuários.",
          variant: "destructive",
        })
        return
      }

      setCarregando(true)
      setErro(null)
      setErroDetalhado(null)
      addDebugLog("Iniciando carregamento de dados...")

      try {
        // Definir um timeout para evitar carregamento infinito
        const timeoutId = setTimeout(() => {
          setErro("Tempo limite excedido ao carregar permissões. Tente novamente.")
          setCarregando(false)
        }, 15000) // 15 segundos de timeout

        // Carregar dados do usuário
        const dadosUsuario = await usuarioService.buscarUsuario(Number(id))

        if (!dadosUsuario) {
          clearTimeout(timeoutId)
          setErro("Usuário não encontrado")
          setCarregando(false)
          return
        }

        setUsuario(dadosUsuario)
        addDebugLog(`Dados do usuário carregados: ${JSON.stringify(dadosUsuario)}`)

        // Verificar se o usuário tem permissões
        let permissoesArray: string[] = []
        if (dadosUsuario.permissoes && Array.isArray(dadosUsuario.permissoes)) {
          addDebugLog(`Permissões encontradas no usuário: ${JSON.stringify(dadosUsuario.permissoes)}`)
          permissoesArray = dadosUsuario.permissoes
        } else {
          addDebugLog(`Usuário não tem permissões ou formato inválido: ${JSON.stringify(dadosUsuario.permissoes)}`)
          // Tentar carregar permissões separadamente
          try {
            const permissoesUsuario = await usuarioService.obterPermissoes(Number(id))
            addDebugLog(`Permissões carregadas separadamente: ${JSON.stringify(permissoesUsuario)}`)
            permissoesArray = permissoesUsuario || []
          } catch (permError: any) {
            addDebugLog(`Erro ao carregar permissões separadamente: ${JSON.stringify(permError)}`)
            permissoesArray = []
          }
        }

        // Inicializar permissões - esta função agora verifica o localStorage primeiro
        const permissoes = inicializarPermissoes(permissoesArray)

        // Atualizar os estados
        setPermissoesSelecionadas(permissoes)

        // Guardar estado original para comparação (sempre da API)
        const permissoesOriginaisAPI = criarPermissoesVazias()
        if (Array.isArray(permissoesArray)) {
          permissoesArray.forEach((perm) => {
            if (perm in permissoesOriginaisAPI) {
              permissoesOriginaisAPI[perm] = true
            }
          })
        }
        setPermissoesOriginais(permissoesOriginaisAPI)

        clearTimeout(timeoutId)
        setCarregando(false)
      } catch (error: any) {
        console.error("Erro ao carregar dados:", error)
        addDebugLog(`Erro ao carregar dados: ${JSON.stringify(error)}`)

        // Capturar mensagem de erro detalhada
        let mensagemErro = "Erro ao carregar dados. Tente novamente."
        let detalhesErro = ""

        if (error.response) {
          mensagemErro = `Erro ${error.response.status}: ${error.response.statusText}`
          detalhesErro = JSON.stringify(error.response.data || {}, null, 2)
        } else if (error.message) {
          mensagemErro = error.message
          detalhesErro = error.stack || ""
        }

        setErro(mensagemErro)
        setErroDetalhado(detalhesErro)
        setCarregando(false)

        // Incrementar tentativas e tentar novamente se necessário
        const novasTentativas = tentativasCarregamento + 1
        setTentativasCarregamento(novasTentativas)

        if (novasTentativas < 3) {
          // Tentar novamente após um intervalo
          setTimeout(() => {
            carregarDados()
          }, 2000)
        }
      }
    }

    carregarDados()
  }, [id, router, toast, tentativasCarregamento, canManagePermissions, inicializarPermissoes, addDebugLog])

  // Salvar permissões no localStorage para persistência entre recarregamentos
  // Substitua o useEffect que salva no localStorage por esta versão mais robusta:
  useEffect(() => {
    if (typeof window !== "undefined" && id && Object.keys(permissoesSelecionadas).length > 0 && !carregando) {
      const storageKey = `permissoes_usuario_${id}`
      localStorage.setItem(storageKey, JSON.stringify(permissoesSelecionadas))

      // Contar quantas permissões estão ativas
      const permissoesAtivas = Object.entries(permissoesSelecionadas)
        .filter(([_, value]) => value === true)
        .map(([key]) => key)

      addDebugLog(`PERSISTÊNCIA: Permissões salvas no localStorage com chave: ${storageKey}`)
      addDebugLog(
        `PERSISTÊNCIA: Permissões ativas salvas (${permissoesAtivas.length}): ${JSON.stringify(permissoesAtivas)}`,
      )
    }
  }, [permissoesSelecionadas, id, addDebugLog, carregando])

  // Nova função para alternar permissão usando o objeto de estado
  const handleTogglePermissao = useCallback(
    (permissaoId: string) => {
      addDebugLog(`Alternando permissão: ${permissaoId}, valor atual: ${permissoesSelecionadas[permissaoId]}`)

      // Usar uma função de atualização de estado para garantir que o estado seja atualizado corretamente
      setPermissoesSelecionadas((prev: Record<string, boolean>) => {
        const novoEstado = {
          ...prev,
          [permissaoId]: !prev[permissaoId],
        }
        addDebugLog(`Novo estado para ${permissaoId}: ${novoEstado[permissaoId]}`)
        return novoEstado
      })
    },
    [permissoesSelecionadas, addDebugLog],
  )

  // Função para selecionar/desmarcar todas as permissões de uma categoria
  const handleToggleCategoria = useCallback(
    (categoria: string, selecionar: boolean) => {
      setPermissoesSelecionadas((prev: Record<string, boolean>) => {
        const novoEstado = { ...prev }
        permissoesPorCategoria[categoria].forEach((perm) => {
          novoEstado[perm.id] = selecionar
        })
        return novoEstado
      })
      addDebugLog(`${selecionar ? "Selecionando" : "Desmarcando"} todas as permissões da categoria ${categoria}`)
    },
    [addDebugLog],
  )

  const handleSalvar = async () => {
    if (!id || !canManagePermissions) return

    setSalvando(true)
    setErro(null)
    setErroDetalhado(null)
    addDebugLog("Iniciando salvamento de permissões...")

    try {
      // Converter o objeto de permissões para um array
      const permissoesArray = Object.entries(permissoesSelecionadas)
        .filter(([_, selecionada]) => selecionada)
        .map(([permissao]) => permissao)

      addDebugLog(`Permissões a serem salvas: ${JSON.stringify(permissoesArray)}`)

      // Usar o método atualizado para salvar permissões
      await usuarioService.atualizarPermissoes(Number(id), permissoesArray)

      // Após salvar com sucesso, atualizar as permissões originais
      setPermissoesOriginais({ ...permissoesSelecionadas })
      addDebugLog("Permissões originais atualizadas após salvamento bem-sucedido")

      toast({
        title: "Permissões atualizadas",
        description: "As permissões do usuário foram atualizadas com sucesso.",
      })

      router.push("/usuarios")
    } catch (error: any) {
      console.error("Erro ao salvar permissões:", error)
      addDebugLog(`Erro ao salvar permissões: ${JSON.stringify(error)}`)

      // Capturar mensagem de erro detalhada
      let mensagemErro = "Erro ao salvar permissões. Tente novamente."
      let detalhesErro = ""

      if (error.response) {
        mensagemErro = `Erro ${error.response.status}: ${error.response.statusText}`
        detalhesErro = JSON.stringify(error.response.data || {}, null, 2)
      } else if (error.message) {
        mensagemErro = error.message
        detalhesErro = error.stack || ""
      }

      setErro(mensagemErro)
      setErroDetalhado(detalhesErro)

      toast({
        title: "Erro",
        description: "Não foi possível atualizar as permissões do usuário.",
        variant: "destructive",
      })
    } finally {
      setSalvando(false)
    }
  }

  // Substitua a função handleTestarEstado por esta versão mais detalhada:
  const handleTestarEstado = () => {
    // Contar quantas permissões estão ativas no estado atual
    const permissoesAtivas = Object.entries(permissoesSelecionadas)
      .filter(([_, value]) => value === true)
      .map(([key]) => key)

    addDebugLog(
      `TESTE: Permissões ativas no estado atual (${permissoesAtivas.length}): ${JSON.stringify(permissoesAtivas)}`,
    )

    // Verificar localStorage
    if (typeof window !== "undefined" && id) {
      const storageKey = `permissoes_usuario_${id}`
      const savedPermissoes = localStorage.getItem(storageKey)
      if (savedPermissoes) {
        try {
          const parsedPermissoes = JSON.parse(savedPermissoes)
          const permissoesAtivasStorage = Object.entries(parsedPermissoes)
            .filter(([_, value]) => value === true)
            .map(([key]) => key)

          addDebugLog(
            `TESTE: Permissões ativas no localStorage (${permissoesAtivasStorage.length}): ${JSON.stringify(permissoesAtivasStorage)}`,
          )

          // Verificar se o estado atual e o localStorage estão sincronizados
          const estaoSincronizados =
            JSON.stringify(permissoesAtivas.sort()) === JSON.stringify(permissoesAtivasStorage.sort())
          addDebugLog(`TESTE: Estado atual e localStorage estão sincronizados: ${estaoSincronizados}`)

          // Verificar diferenças específicas
          if (!estaoSincronizados) {
            const somenteNoEstado = permissoesAtivas.filter((p) => !permissoesAtivasStorage.includes(p))
            const somenteNoStorage = permissoesAtivasStorage.filter((p) => !permissoesAtivas.includes(p))

            if (somenteNoEstado.length > 0) {
              addDebugLog(`TESTE: Permissões apenas no estado atual: ${JSON.stringify(somenteNoEstado)}`)
            }

            if (somenteNoStorage.length > 0) {
              addDebugLog(`TESTE: Permissões apenas no localStorage: ${JSON.stringify(somenteNoStorage)}`)
            }
          }
        } catch (e) {
          addDebugLog(`TESTE: Erro ao analisar permissões do localStorage: ${e}`)
        }
      } else {
        addDebugLog(`TESTE: Nenhuma permissão encontrada no localStorage para o usuário ${id}`)
      }
    }

    // Verificar alterações não salvas (comparando com as permissões originais)
    const alteracoesNaoSalvas = temAlteracoes()
    addDebugLog(`TESTE: Existem alterações não salvas: ${alteracoesNaoSalvas}`)

    if (alteracoesNaoSalvas) {
      const permissoesAlteradas = Object.entries(permissoesSelecionadas)
        .filter(([key, value]) => value !== permissoesOriginais[key])
        .map(([key]) => key)

      addDebugLog(`TESTE: Permissões alteradas: ${JSON.stringify(permissoesAlteradas)}`)
    }

    toast({
      title: "Estado das permissões",
      description: `${permissoesAtivas.length} permissões ativas. ${alteracoesNaoSalvas ? "Existem alterações não salvas." : "Sem alterações pendentes."}`,
    })
  }

  // Adicione esta função para limpar o localStorage e reiniciar o estado
  const handleLimparLocalStorage = () => {
    if (typeof window !== "undefined" && id) {
      const storageKey = `permissoes_usuario_${id}`
      localStorage.removeItem(storageKey)
      addDebugLog(`LIMPEZA: Permissões removidas do localStorage com chave: ${storageKey}`)

      // Recarregar a página para reiniciar o estado
      window.location.reload()
    }
  }

  // Se o usuário não tem permissão para gerenciar permissões, mostrar mensagem
  if (!canManagePermissions) {
    return (
      <div className="container mx-auto py-6">
        <div className="flex items-center gap-4 mb-6">
          <BackButton />
          <h1 className="text-2xl font-bold">Gerenciar Permissões</h1>
        </div>

        <Alert variant="destructive" className="mb-4">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>Você não tem permissão para gerenciar permissões de usuários.</AlertDescription>
        </Alert>
      </div>
    )
  }

  return (
    <div className="container mx-auto py-6">
      <div className="flex justify-between items-center mb-6">
        <div className="flex items-center gap-4">
          <BackButton />
          <h1 className="text-2xl font-bold">Gerenciar Permissões</h1>
        </div>

        <div className="flex gap-2">
          <Button variant="outline" onClick={handleTestarEstado}>
            Testar Estado
          </Button>

          {!carregando && !erro && (
            <Button
              onClick={handleSalvar}
              disabled={salvando}
              className={temAlteracoes() ? "bg-yellow-600 hover:bg-yellow-700" : ""}
            >
              {salvando ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Salvando...
                </>
              ) : temAlteracoes() ? (
                "Salvar Alterações"
              ) : (
                "Salvar Permissões"
              )}
            </Button>
          )}
        </div>
      </div>

      {erro && (
        <Alert variant="destructive" className="mb-4">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>
            {erro}
            {erroDetalhado && (
              <div className="mt-2">
                <Button variant="outline" size="sm" onClick={() => setMostrarErroCompleto(!mostrarErroCompleto)}>
                  {mostrarErroCompleto ? "Ocultar detalhes" : "Mostrar detalhes"}
                </Button>
                {mostrarErroCompleto && (
                  <pre className="mt-2 p-2 bg-gray-100 rounded text-xs overflow-auto max-h-40">{erroDetalhado}</pre>
                )}
              </div>
            )}
            <Button
              variant="outline"
              className="mt-2"
              onClick={() => {
                setErro(null)
                setErroDetalhado(null)
                setCarregando(true)
                setTentativasCarregamento(0)
              }}
            >
              Tentar novamente
            </Button>
          </AlertDescription>
        </Alert>
      )}

      {carregando ? (
        <Card>
          <CardContent className="pt-6">
            <div className="flex flex-col items-center justify-center p-8">
              <Loader2 className="h-12 w-12 animate-spin mb-4" />
              <p className="text-lg">Carregando permissões...</p>
            </div>
          </CardContent>
        </Card>
      ) : (
        <>
          {usuario && (
            <Card className="mb-6">
              <CardHeader>
                <CardTitle>Usuário: {usuario.nome}</CardTitle>
                <CardDescription>Username: {usuario.username}</CardDescription>
              </CardHeader>
            </Card>
          )}

          {Object.entries(permissoesPorCategoria).map(([categoria, listaPermissoes]) => (
            <Card key={categoria} className="mb-6">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle>{categoria}</CardTitle>
                <div className="flex gap-2">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleToggleCategoria(categoria, true)}
                    className="text-xs h-8"
                  >
                    Selecionar todos
                  </Button>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleToggleCategoria(categoria, false)}
                    className="text-xs h-8"
                  >
                    Desmarcar todos
                  </Button>
                </div>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {listaPermissoes.map((permissao) => {
                    // Verificar se a permissão está selecionada
                    const isChecked = !!permissoesSelecionadas[permissao.id]
                    const isChanged = permissaoAlterada(permissao.id)

                    return (
                      <div key={permissao.id} className="flex items-center space-x-2">
                        {/* Substituir checkbox por botão estilizado como checkbox */}
                        <button
                          type="button"
                          onClick={() => handleTogglePermissao(permissao.id)}
                          className={`flex items-center justify-center w-5 h-5 rounded border ${
                            isChecked
                              ? isChanged
                                ? "bg-yellow-600 border-yellow-600"
                                : "bg-blue-600 border-blue-600"
                              : isChanged
                                ? "bg-white border-yellow-600"
                                : "bg-white border-gray-300"
                          }`}
                          aria-checked={isChecked}
                          role="checkbox"
                        >
                          {isChecked && <Check className="h-3 w-3 text-white" />}
                        </button>
                        <label
                          onClick={() => handleTogglePermissao(permissao.id)}
                          className={`text-sm font-medium cursor-pointer ${
                            isChanged ? "text-yellow-600 font-bold" : ""
                          }`}
                        >
                          {permissao.label}
                        </label>
                      </div>
                    )
                  })}
                </div>
              </CardContent>
            </Card>
          ))}

          {/* Área de debug - mostrada por padrão */}
          <Card className="mb-6 border border-gray-300">
            <CardHeader className="bg-gray-200 cursor-pointer" onClick={() => setMostrarDebug(!mostrarDebug)}>
              <CardTitle className="flex items-center justify-between">
                <span>Informações de Debug</span>
                {mostrarDebug ? <ChevronUp className="h-5 w-5" /> : <ChevronDown className="h-5 w-5" />}
              </CardTitle>
            </CardHeader>
            {mostrarDebug && (
              <CardContent className="bg-gray-100">
                {/* Adicione este botão na área de debug */}
                {/* Dentro do CardContent da área de debug, adicione este botão junto ao botão "Limpar": */}
                <div className="flex justify-end mb-2 gap-2">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={handleLimparLocalStorage}
                    className="bg-red-100 hover:bg-red-200"
                  >
                    Limpar localStorage
                  </Button>
                  <Button variant="outline" size="sm" onClick={() => setDebugInfo("")}>
                    Limpar
                  </Button>
                </div>
                <div className="bg-white p-4 rounded-md border border-gray-300">
                  <h3 className="font-bold mb-2 text-gray-800">Estado atual das permissões:</h3>
                  <pre className="text-xs overflow-auto max-h-40 mb-4 bg-gray-50 p-2 rounded border border-gray-200 text-gray-800">
                    {JSON.stringify(permissoesSelecionadas, null, 2)}
                  </pre>

                  <h3 className="font-bold mb-2 text-gray-800">Log de eventos:</h3>
                  <pre className="text-xs overflow-auto max-h-40 bg-gray-50 p-2 rounded border border-gray-200 text-gray-800">
                    {debugInfo}
                  </pre>
                </div>
              </CardContent>
            )}
          </Card>

          {!carregando && !erro && (
            <div className="flex justify-end mt-6 space-x-4">
              <Button variant="outline" onClick={() => router.push("/usuarios")}>
                Cancelar
              </Button>
              <Button
                onClick={handleSalvar}
                disabled={salvando}
                className={temAlteracoes() ? "bg-yellow-600 hover:bg-yellow-700" : ""}
              >
                {salvando ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Salvando...
                  </>
                ) : temAlteracoes() ? (
                  "Salvar Alterações"
                ) : (
                  "Salvar Permissões"
                )}
              </Button>
            </div>
          )}
        </>
      )}
    </div>
  )
}
